.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.top-sale-list {
  margin: 3% 10%;
  padding: 0;
}

.top-sale-item {
  background-color: #ece2c6;
  border-style: double;
  border-color: #ae6d5a;
  border-width: 2px;
  display: flex;
  padding: 1em;
  align-items: center;
  justify-content: center;
}

.item-detail {
  display: block;
  text-align: left;
  margin-left: 5%;
}

.list-data {
  display: contents;
}

.option-image:hover {
  cursor: pointer;
}

.content-control {
  display: flex;
}

.content-option {
  margin-left: 2px;
}

.content-option p {
  margin: 0 2em;
}

.page {
  color: #d1d1c9;
  background-color: #414141;
  padding: 0.5rem;
  margin-left: 2rem;
  border-style: outset;
  border-color: rgb(255, 217, 0);
  border-width: 2px;
  border-radius: 5px;
}

.option {
  margin: auto 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.option-image {
  width: 5rem;
}

button:disabled {
  color: #d1d1c9;
  background-color: #949494;
  padding: 0.5rem;
  margin-left: 2rem;
  border-style: outset;
  border-color: rgb(105, 105, 105);
  border-width: 2px;
  border-radius: 5px;
}

button:hover {
  color: #414141;
  background-color: rgb(255, 217, 0);
  padding: 0.5rem;
  border-style: outset;
  border-color: rgb(174, 134, 0);
  border-width: 2px;
  border-radius: 5px;
}

.file-upload {
  color: #d1d1c9;
  background-color: #414141;
  padding: 0.5rem;
  margin-bottom: 2rem;
  border-style: outset;
  border-color: rgb(255, 217, 0);
  border-width: 2px;
  border-radius: 5px;
  resize: both;
  min-width: 40%;
  height: 100%;
}

.items-input {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#page-control {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}

#title-image {
  width: 100%;
  margin-bottom: 2rem;
}

#data-center {
  color: #d1d1c9;
  background-color: #414141;
  padding: 0.5rem;
  margin-left: 2rem;
  border-style: outset;
  border-color: rgb(255, 217, 0);
  border-width: 2px;
  border-radius: 5px;
}

#amount-to-display {
  margin-left: 2rem;
  color: #d1d1c9;
  background-color: #414141;
  padding: 0.5rem;
  border-style: outset;
  border-color: rgb(255, 217, 0);
  border-width: 2px;
  border-radius: 5px;
}

#data-center-selection {
  display: flex;
}
#display-amount {
  display: flex;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
